.addContent{
    padding: 20px;
    /* margin-left: 10px;
    margin-right: 10px; */
    background: #fff; 
}
.addContent h2{
    font-size: 22px;
    text-align: center;
    letter-spacing: 1px;
}
.addContent .div-card{
  padding-top: 15px;
    width: 90%;
    margin-bottom: 15px;
    /* border: 2px solid #d1d0d0; */
    /* border-radius: 0px 0 30px 0; */
}
.addContent .div-card .div-card-head{
    display: flex;
    margin: auto;
}
.addContent .div-card img{
    height: 18px;
    width: 25px;
    margin-top: 8px;
    transform: scaleX(-1);
    
}
.addContent .div-card h3{
    font-size: 17px;
    letter-spacing: 1px;
    padding-left: 5px;
    color: #555555;
    font-family: "Lato", sans-serif;
}
.addContent .div-card hr{
    margin-top: 2px;
   
}
.addContent .div-card .div-card-row{
    display: flex;
    width: 100%;
}
.addContent .div-card .div-col-left,
.addContent .div-card .div-col-right {
    width: 50%;
}
.addContent .div-card .form-box{
    font-size: 12.5px;
    color: #646464;
    padding-top: 6px;
    padding-bottom: 6px;
}
.addContent .div-card .div-label,
.addContent .div-card .div-input ,
.addContent .div-card .div-select {
    padding-left: 6px;
    padding-right: 6px;
    margin-bottom: 5px;
}
.addContent .div-card input,
.addContent .div-card select,
.addContent .div-card textarea {
    border: 1px solid #c2c2c2;
    font-size: 12px;
    width: 90%;
    padding-left: 5px;
    padding-right: 5px;
}
.addContent .div-card input:focus{
    outline: none;  
}

.addContent .div-card .submit-btn{
    font-size: 14px;
    padding-top: 10px;
    padding-bottom: 10px;
    background: #eb2661;
    width: 20%;
    text-align: center;
    border: none;
    color: #f9fafc;
}
.addContent .div-card .submit-btn:focus {
    border: none;
    outline: none;
}
.addContent .div-card .submit-btn:disabled{
    background-color: #646464;
}