@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.12.1/css/all.min.css";

.dashboard {
    margin: 0;
    padding: 0;
    font-family: "Roboto",
        sans-serif;
}

.dashboard header {
    position: fixed;
    background: #000 !important;
    padding: 8px;
    width: 100%;
    height: 45px;
    display: flex;
    margin: auto;
    z-index: 9999;
}

.dashboard .left_area h3 {
    color: #fff;
    /* margin-top: -20px; */
    margin: 0 0 0 50px;
    /* margin-bottom: -20px; */
    font-size: 20px;
    font-weight: 900;
    width: auto;
    font-family: 'Philosopher',
        sans-serif;
    letter-spacing: 2px;
}

.dashboard .left_area span {
    color: #eb2661;
    font-family: 'Philosopher', sans-serif;
    letter-spacing: 2px;
}

.dashboard .logout_btn {
    padding-top: 3px;
    padding-bottom: 3px;
    padding-left: 7px;
    padding-right: 7px;
    background: #eb2661;
    text-decoration: none;
    float: right;
    margin-top: -35px;
    margin-right: 40px;
    border-radius: 2px;
    font-size: 13px;
    color: #fff;
    transition: 0.5s;
    transition-property: background;
    outline: none;
    border: 4px;
}

.dashboard .logout_btn:hover {
    background: #eb2661;
}

.dashboard .sidebar {
    /* background: #2f323a; */
    background: url('../assets/sidebar_image.png') no-repeat !important;
    background-size: cover;
    margin-top: 45px;
    padding-top: 30px;
    position: fixed;
    left: 0;
    width: 250px;
    height: 100%;
    transition: 0.5s;
    transition-property: left;
}

.dashboard .sidebar .dashboard-profile {
    display: flex;
    margin-right: 10px;
    margin-left: 10px;
    padding-right: 5px;
    padding-left: 5px;
}

.dashboard .sidebar .profile_image {
    width: 50px;
    height: 50px;
    border-radius: 100px;
    /* margin-bottom: 10px; */
}

.dashboard .sidebar h4 {
    color: #ccc;
    margin-top: auto;
    margin-bottom: auto;
    text-align: center;
    font-size: 16px;
    font-family: "Lato", sans-serif;
    padding-left: 20px;
}

.dashboard .sidebar hr {
    margin-left: 15px;
    margin-right: 15px;
    border-bottom: 1px solid #c5c5c5;
}

.dashboard .sidebar a {
    color: #ccc;
    display: block;
    width: 100%;
    letter-spacing: 1px;
    line-height: 10px;
    padding-top: 15px;
    padding-bottom: 15px;
    text-decoration: none;
    padding-left: 40px;
    box-sizing: border-box;
    transition: 0.5s;
    transition-property: background;
}

.dashboard .sidebar a:hover {
    background: #eb2661;
    color: #fff;
}

.dashboard .sidebar i {
    padding-right: 10px;
}

.dashboard label #sidebar_btn {
    z-index: 1;
    color: #fff;
    position: fixed;
    cursor: pointer;
    /* left: 300px; */
    font-size: 20px;
    margin: -10px 0;
    transition: 0.5s;
    transition-property: color;
}

.dashboard label #sidebar_btn:hover {
    color: #eb2661;
}

.dashboard #check:checked~.sidebar {
    left: -190px;
}

.dashboard #check:checked~.sidebar a span {
    display: none;
}

.dashboard #check:checked~.sidebar a {
    font-size: 20px;
    margin-left: 170px;
    width: 80px;
}

.dashboard .content {
    margin-left: 250px;
    background: #f0f0f0;
    background-position: center;
    background-size: cover;
    height: 100vh;
    transition: 0.5s;
    padding-top: 50px;
}

.dashboard #check:checked~.content {
    margin-left: 30px;
}

.dashboard #check {
    display: none;
}