
.dash-home {
    padding: 0;
    display: block;
    overflow-y: hidden;
}

.dash-home h3 a{
    font-size: 13px;
    letter-spacing: 1px;
    padding-left: 5px;
    color: #3b4046;
    cursor: pointer;
}

.dash-home h3 a{
    text-decoration: none;
    /* color: #202026; */
}
.dash-home h3 a:hover{
    color: #5412a0;
}

.dash-home .dash-title {
    margin-left: 15px;
}

.dash-home .dash-title h2 {
    font-size: 25px;
    letter-spacing: 1px;
    padding-left: 5px;
    color: #202026;
    cursor: pointer;
    font-family: 'Heletica Now Display';
    margin-bottom: 6px;
}

.dash-home .dash-title .sub-title{
    display: flex;
    padding-left: 10px;
}

.dash-home .dash-title .sub-title span{
    font-size: 12px;
    color: #3b4046;
    text-transform: uppercase;
    cursor: pointer;
}
.dash-home .dash-title .sub-title span:hover{
    color: #5412a0;
}

.dash-home .dash-title .sub-title a{
    color: #5412a0;
    text-decoration: none;
    font-size: 12px;
    padding: 0 15px 0 25px;
}




.dash-home .div-card-row{
    margin-top: 40px; 
}
.dash-home .card-list{
    padding: 12px;
    background: #dddddd;
}



.dash-home .div-card-row .content-cards {
    display: flex;
    /* grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 1rem; */
    padding-top: 15px;
}

.dash-home .div-card-row .content-cards .card-single {
    background: #f9fafc;
    border-radius: 7px;
    padding: 15px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.15);
    cursor: pointer;
    margin-bottom: 15px;
}

.dash-home .div-card-row .content-cards .card-single .card-body {
    padding: 8px;
    display: block;
    align-items: center;
}
.dash-home .div-card-row .content-cards .card-single .card-body span{
    font-size: 15px;
    font-family: "Poppins", sans-serif;
    color: #333;
}
.dash-home .div-card-row .content-cards .card-single .card-body i {
    font-size: 18px;
    color: #686868;
    padding-right: 6px;
}

.dash-home .div-card-row .content-cards .card-single h5 {
    color: #646464;
    font-size: 16px;
    padding-left: 15px;
}

.dash-home .div-card-row .content-cards .card-single:hover h5 {
    color: #eb2661;
}

.dash-home .div-card-row  .content-cards .card-single .card-footer {
    padding: 8px;
    background: #f9fafc;
    border-top: none;
}

.dash-home .div-card-row .content-cards .card-single a {
    color: #eb2661;
    float: right;
    /* margin-right: 20px; */
    width: 100%;
    text-align: center;
    padding-top: 8px;
    padding-bottom: 8px;
    border: none;
    background: #f9fafc;
}


.dash-home .div-card-row .user-card {
   
    background: #fff;
    border-radius: 7px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
    
}
.dash-home .div-card-row .user-card h3 {
    font-size: 14px;
    font-family: "Lato", sans-serif;
    padding-top: 15px;
    padding-bottom: 10px;
    font-weight: 600;
    padding-left: 10px;
}
.dash-home .div-card-row .user-card table {
    width: 100%;
    border-collapse: collapse;
}

.dash-home .div-card-row .user-card table thead {
    background: #efefef;
    text-align: left;
}

.dash-home .div-card-row .user-card table td,
.dash-home .div-card-row .user-card table th {
    font-size: 13.5px;
    padding: 10px 10px;
    color: #333333;
}

.dash-home .div-card-row .user-card table td {
    font-size: 12px;
}

.dash-home .div-card-row .user-card table tbody tr:nth-child(even) {
    background: #f9fafc;
}
.dash-home .div-card-row .row{
    display: flex;
}
.dash-home .div-card-row .col-sm-7{
    width: 65%;
    margin-right: 15px;
}
.dash-home .div-card-row .col-sm-5{
    width: 35%;
    margin-left: 15px;
}
.dash-home .div-card-row .col-sm-5 .list-unstyled{
    list-style: none;
    padding-left: 0;
}
.dash-home .div-card-row .col-sm-5 .list-unstyled .media{
    margin-bottom: 10px;
    display: flex;
}
.dash-home .div-card-row .col-sm-5 .list-unstyled .media .media-body{
    padding-left: 15px;
}
.dash-home .div-card-row .col-sm-5 .list-unstyled img {
    height: 55px;
    width: 55px;
    display: block;
    background-size: cover;
    object-fit: cover;
}

.dash-home .div-card-row .col-sm-5 .list-unstyled h5 {
    font-family: "Lato", sans-serif;
    font-size: 13.5px;
    letter-spacing: 1px;
    font-weight: 600;
    color: #222;
    margin-bottom: 8px;
    margin-top: 0;
}

.dash-home .div-card-row .col-sm-5 .list-unstyled p {
    font-family: "Poppins",
        sans-serif;
    font-size: 10.5px;
    color: #646464;
    margin-bottom: 0;
}
.dash-home .div-card-row .col-sm-5 .list-unstyled .btn-link{
    text-align: center;
    text-decoration: none;
    padding-top: 20px;
    padding-bottom: 10px;
}
.dash-home .div-card-row .col-sm-5 .list-unstyled .btn-link a {
    text-decoration: none;
    background-color: #eb2661;
    color: #eee;
    padding: 5px 10px;
    font-size: 10px;
    border-radius: 2px;
}

.dash-home .div-card-row .owl-carousel .owl-item .card{
    background: #fff;
    width: 200px;
    padding: 10px;

}
.dash-home .div-card-row .owl-carousel .owl-item .card img{
    height: 120px;
}
.dash-home .div-card-row .owl-carousel .owl-item .card p{
    font-size: 12px;
}
.dash-home .div-card-row .owl-carousel .owl-item .card span {
    font-family: "Poppins",
    sans-serif;
    font-size: 10.5px;
    color: #646464;
    margin-bottom: 0;
}
.dash-home .div-card-row .owl-carousel .owl-item .card a{
    font-family: "Poppins",
    sans-serif;
    font-size: 10.5px;
    color: #222;
    margin-bottom: 0;
    text-decoration: none;
}