.page-header a {
    text-decoration: none;
}

.page-header ul {
    list-style: none;
}

.page-header p {
    font-size: 1.6rem;
}
.page-header .navbar-toggler{
    display: none;
}

.page-header .div-header {
    background: #1a1919;
    scroll-snap-align: start;
}

.page-header header {
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    /* scroll-snap-align: start; */
}

.page-header .container {
    margin: 0 auto;
    padding: 0 1.5rem;
}

.page-header nav {
    width: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 0;
    /* border-bottom: 1px solid rgba(255, 255, 255, .2); */
}

.page-header nav .wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.page-header .nav-brand {
    margin-top: auto;
    margin-bottom: auto;
    display: block;
    width: auto;
}

.page-header .nav-brand a {
    padding-left: 20px;
    font-size: 13px;
    text-transform: uppercase;
    color: #eee;
    letter-spacing: 1px;
    margin-top: auto;
    margin-bottom: auto;
}

.page-header .bg-light {
    background-color: transparent !important;
    position: relative;
}

.page-header .nav-list {
    margin-top: 0;
    display: flex;
    margin-bottom: 0;
    right: 0;
    left: 0;
    z-index: 1500;
    /* transition: all 650ms ease-in-out; */
    padding-left: 0;
}

.page-header .nav-list .active {
    right: 0;
}

.page-header .nav-link {
    font-size: 16px;
    text-transform: uppercase;
    color: #fff;
    font-size: 12.5px;
    letter-spacing: 1px;
    /* color: rgba(0,0,0,0.7); */
}

.page-header .nav-item:hover .drop-menu {
    opacity: 1;
    visibility: visible;
    transition: all 0.4s ease;
    top: 35px;
}

.page-header .nav-item .drop-menu {
    background: #0f0f0f;
    /* background: #030303; */
    padding-left: 0;
    position: absolute;
    width: 120px;
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    top: 45px;
    margin-left: -15px;
}

.page-header .nav-item .drop-menu .drop-item {
    text-align: center;
    padding: 10px 15px;

}

.page-header .nav-item .drop-menu .drop-item:hover {
    background: #1d1d1d;
    transition: .5s ease;
}

.page-header .nav-item .drop-menu .drop-link {
    font-size: 11px;
    text-transform: uppercase;
    color: #eee;
    text-decoration: none;
    letter-spacing: 2px;
    display: block;
}

.page-header .nav-item .mega-drop {
    /* background: #030303; */
    padding-left: 0;
    position: absolute;
    width: 100%;
    left: 0;
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    top: 45px;
}

.page-header .nav-item .mega-drop .content {
    background: #0f0f0f;
    padding: 10px;
    display: flex;
    max-width: 100%;
    justify-content: space-between;
}

.page-header .nav-item .mega-drop .row {
    width: calc(25% - 5px);
    line-height: 15px;
    margin: 0;
}

.page-header .nav-item .mega-drop .row img {
    width: 140px;
    height: 100px;
    object-fit: cover;
    margin: auto;
    display: block;
}

.page-header .nav-item .mega-drop .row .mega-header {
    font-size: 11px;
    text-transform: uppercase;
    color: #eee;
    letter-spacing: 2px;
    padding-bottom: 8px;
    text-align: center;
}

.page-header .nav-item .mega-drop .row .drop-item {
    padding-top: 4px;
    padding-bottom: 4px;
    height: auto;
    width: auto;

    text-align: center;
}

.page-header .nav-item .mega-drop .row .drop-link {
    font-size: 9px;
    text-transform: uppercase;
    color: #eee;
    text-decoration: none;
    letter-spacing: 2px;
    font-weight: 0;
}

.page-header .nav-item .mega-drop .drop-item:hover {
    background: #1d1d1d;
    transition: .5s ease;
}

.page-header .nav-item:hover .mega-drop {
    opacity: 1;
    visibility: visible;
    transition: all 0.4s ease;
    top: 35px;
}

