.dash-register {
    padding-top: 30px;
    padding-bottom: 20px;
    padding-left: 50px;
    padding-right: 30px;
    display: block;
    overflow-y: hidden;
    
    background: url('../assets/homeaboutbanner.jpg');
    background-size: cover;
    background-attachment: fixed;
}

.dash-register h3 {
    font-size: 30px;
    font-family: 'Philosopher',
        sans-serif;
    letter-spacing: 2px;
    padding-left: 5px;
    color: #333;
    /* text-align: center; */
}

.dash-register h3 span {
    color: #eb2661;
    font-family: 'Philosopher',
        sans-serif;
    letter-spacing: 2px;
}

.dash-register .div-card-row {
    display: flex;

    padding-top: 20px;

}

.dash-register .div-card-row .div-col-left,
.dash-register .div-card-row .div-col-right {
    width: 40%;
    display: inline-block;
}

.dash-register .div-card-row .form-box {
    font-size: 12.5px;
    color: #646464;
    padding-top: 6px;
    padding-bottom: 6px;
}

.dash-register .div-card-row .div-label,
.dash-register .div-card-row .div-input,
.dash-register .div-card-row .div-select {
    padding-left: 6px;
    padding-right: 6px;
    margin-bottom: 5px;
}

.dash-register .div-card-row input,
.dash-register .div-card-row select {
    border: 1px solid #c2c2c2;
    font-size: 12px;
    width: 90%;
    padding-left: 5px;
    padding-right: 5px;
}

.dash-register .div-card-row input:focus {
    outline: none;
}

.dash-register .div-card-row .div-card .submit-btn {
    font-size: 14px;
    padding-top: 8px;
    padding-bottom: 8px;
    background: #eb2661;
    width: 20%;
    text-align: center;
    border: none;
    color: #f9fafc;
    margin-left: 10px;
}

.dash-register .div-card-row .div-card {
    margin-top: 10px;
}

.dash-register .div-card-row .submit-btn:focus {
    border: none;
    outline: none;
}
