.dash-users {
    padding-top: 30px;
    padding-bottom: 20px;
    padding-left: 50px;
    padding-right: 30px;
    display: block;
    overflow-y: hidden;
    background: #f0f0f0;
}

.dash-users h3 {
    font-size: 30px;
    font-family: 'Philosopher',
        sans-serif;
    letter-spacing: 2px;
    padding-left: 5px;
    color: #333;
    /* text-align: center; */
}

.dash-users h3 span {
    color: #eb2661;
    font-family: 'Philosopher',
        sans-serif;
    letter-spacing: 2px;
}

.dash-users .div-card-row {
    /* display: block; */
    width: 100%;
    padding-top: 20px;
    margin: 0;
}

.dash-users .div-card-row .col-sm-6{
    width: 50%;
}
.dash-users .div-card-row .col-sm-6 .div-row {
    margin: 0;
    display: flex;
}
.dash-users .div-card-row .col-sm-6 .div-row i {
    color: #eb2661;
    font-size: 18px;
}
.dash-users .div-card-row .col-sm-6 .div-row label{
    
    font-family: "Lato",
    sans-serif;
    color: #333;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 1px;
}
.dash-users .div-card-row .col-sm-6 .div-row p{
    padding-left: 6px;
    font-family: "Poppins",
    sans-serif;
    color: #444;
    font-size: 14px;
    letter-spacing: 1px;
    margin-top: auto;
    margin-bottom: auto;
}


.dash-users .div-card-row h5 {
    padding-left: 6px;
    font-family: "Poppins", sans-serif;
    color: #eb2661;
    font-size: 18px;

    letter-spacing: 1px;
}

.dash-users .div-card-row .form-box {
    font-size: 12.5px;
    color: #646464;
    padding-top: 6px;
    padding-bottom: 6px;
}

.dash-users .div-card-row .div-textarea {
    padding-left: 6px;
    padding-right: 6px;
    margin-bottom: 5px;
}

.dash-users .div-card-row textarea {
    border: 1px solid #c2c2c2;
    font-size: 12px;
    width: 90%;
    padding-left: 5px;
    padding-right: 5px;
}

.dash-users .div-card-row textarea:focus {
    outline: none;
}

.dash-users .div-card-row .submit-btn {
    font-size: 14px;
    padding-top: 8px;
    padding-bottom: 8px;
    background: #eb2661;
    width: 20%;
    text-align: center;
    border: none;
    color: #f9fafc;

    display: block;
    margin: auto;
}

.dash-users .div-card-row .submit-btnn {
    font-size: 14px;
    padding-top: 8px;
    padding-bottom: 8px;
    background: #eb2661;
    width: 20%;
    text-align: center;
    border: none;
    color: #f9fafc;
    margin-left: 10px;
}

.dash-users .div-card-row .submit-btn:focus {
    border: none;
    outline: none;
}
