.div-content{
    padding-top: 30px;
    padding-bottom: 20px;
    padding-left: 50px;
    padding-right: 30px;
    display: block;
    overflow-y: hidden;
    background: #f0f0f0;
}
.div-content h3{
    font-size: 30px;
    font-family: 'Philosopher',
    sans-serif;
    letter-spacing: 2px;
    padding-left: 5px;
    color: #333;
    /* text-align: center; */
}
.div-content h3 span{
    color: #eb2661;
    font-family: 'Philosopher',
    sans-serif;
    letter-spacing: 2px;
}
.div-content .content-cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 3rem;
    padding-top: 15px;
}
.div-content .content-cards .card-single{
    background: #f9fafc;
    border-radius: 7px;
    padding: 5px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.15);
    cursor: pointer;
}
.div-content .content-cards .card-single .card-body{
    padding: 8px;
    display: flex;
    align-items: center;
}
.div-content .content-cards .card-single .card-body i{
  font-size: 20px;
  color: #686868;
  padding-right: 6px;
}
.div-content .content-cards .card-single h5{
    color: #646464;
    font-size: 16px;
    padding-left: 15px;
}
.div-content .content-cards .card-single:hover h5 {
    color: #eb2661;
}
.div-content .content-cards .card-single .card-footer{
    padding: 8px;
    background: #f9fafc;
    border-top: none;
}
.div-content .content-cards .card-single  button,
.div-content .content-cards .card-single  a{
    color: #eb2661;
    float: right;
    /* margin-right: 20px; */
    width: 100%;
    text-align: center;
    padding-top: 8px;
    padding-bottom: 8px;
    border: none;
    background: #f9fafc;
}
.div-content .content-cards .card-single button:focus{
    border: none;
    outline: none;
    background: #f9fafc;
}
.div-content .div-container{
    margin-top: 20px;
}
.div-content .submit-btn:disabled {
    background-color: #646464;
}