.login {
    scroll-behavior: smooth;
    align-items: center;
    background: url("../../assets//homebanner4.png");
    background-size: cover;
    background-position: center;
    
    height: 92.2vh;
    
    width: 100%;
    text-transform: uppercase;
    overflow-x: hidden;
}


.login .login-title {
    margin-bottom: 30px;
}

.login h2 {
    color: #eee;
    font-size: 22px;
    letter-spacing: 4px;
    font-family: 'Philosopher',
        sans-serif;
    padding-bottom: 5px;
    margin-bottom: 0;
    text-align: center;
}

.login p {
    color: #afafaf;
    font-size: 9px;
    letter-spacing: 1px;
    font-family: 'Philosopher',
        sans-serif;
    text-transform: lowercase;
    padding-bottom: 5px;
    margin-bottom: 0;
    font-weight: 100;
}

.login .div-login-main {
    /* margin: 0 5%; */
    padding-top: 20px;
    padding-bottom: 20px;
    width: auto;
    transform: translateY(25%);
}

.login .div-login-main .div-form {
    max-width: 35%;
    margin: auto;
    display: block;
    border: 1px solid #3b3b3b;
    padding: 30px;
}

.login .div-login-main .form-box {
    font-size: 11px;
    color: #eee;
    padding-top: 6px;
    padding-bottom: 6px;
    letter-spacing: 2px;
}

.login .div-login-main .div-label,
.login .div-login-main .div-input,
.login .div-login-main.div-select {
    padding-left: 6px;
    padding-right: 6px;
    margin-bottom: 5px;
}

.login .div-login-main input,
.login .div-login-main select {
    border: 1px solid #c2c2c2;
    font-size: 12px;
    width: 90%;
    padding-left: 5px;
    padding-right: 5px;
}

.login .div-login-main input:focus {
    outline: none;
}

.login .div-login-main .div-card .submit-btn {
    font-size: 14px;
    padding-top: 10px;
    padding-bottom: 10px;
    background: #000;
    width: 20%;
    text-align: center;
    border: none;
    color: #f9fafc;
    margin: auto;
    display: block;
}

.login .div-login-main .div-card {
    margin-top: 10px;
}

.login .div-login-main .div-card .submit-btn:focus {
    border: none;
    outline: none;
}

.login .div-login-main .div-form .div-row {
    /* display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center; */
    padding-top: 10px;
    padding-bottom: 10px;
}

.login .div-login-main .div-form .div-row a {
    text-decoration: none;
    color: #f9fafc;
    text-transform: capitalize;
    font-size: 12px;
    letter-spacing: 1px;
}

.login .div-login-main .div-form .div-row .text-left {
    margin-left: 12px;
    float: left;
}

.login .div-login-main .div-form .div-row .text-right {
    float: right;
    margin-right: 45px;
}



@media (min-device-width: 1024px) {

    .login .div-login-main .div-form {
        max-width: 35% !important;
        width: 35% !important;
    }
}

@media (min-device-width: 768px) {

    .login .div-login-main .div-form {
        max-width: 60%;
        width: 60%;
    }
}

@media (min-device-width: 576px) {

    .login .div-login-main .div-form {
        max-width: 80%;
    }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {

    .login .div-login-main .div-form {
        max-width: 100%;
    }

    /* .login .login-banner {
        height: 95vh;
    } */
    .login .div-login-main .div-form {
        margin: auto;
        display: block;
    }

    .login .div-login-main .div-card .submit-btn {
        width: 30%;
        margin-top: 30px;
    }
}